<template>
  <div class="progress-circle">
    <svg :width="radius" :height="radius" viewBox="0 0 100 100">
      <circle cx="50" cy="50" r="50" fill="transparent" class="progress-background"></circle>
      <circle cx="50" cy="50" r="50" fill="transparent" class="progress-bar"
              :stroke-dashoffset="strokeDashOffset"
              :stroke-dasharray="strokeDasharray"></circle>
    </svg>
    <slot></slot>
  </div>
</template>
<script type="text/ecmascript-6">
  /* 
   * 1. 2
   * 2. stroke-dasharray
   * 3. stroke-dashoffsetstroke
   * 4. viewBox
   * */
  export default {
    props: {
      radius: {
        type: Number,
        default: 100
      },
      precent: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        strokeDasharray: 100 * Math.PI
      }
    },
    computed: {
      // svg
      strokeDashOffset() {
        return (1 - this.precent) * this.strokeDasharray
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .progress-circle
    position: relative
    circle
      stroke-width: 8px
      transform-origin: center
      &.progress-background
        transform: scale(0.9)
        stroke: $color-theme-d
      &.progress-bar
        transform: scale(0.9) rotate(-90deg)
        stroke: $color-theme
</style>



